<template>
  <div class="view column">
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('home',true)">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <el-collapse-item
          :title="panelDsl.title"
          :name="panelDsl.title"
          v-for="(panelDsl, panelDslIndex) in APP.dslArray"
          :key="'panelDsl'+panelDslIndex"
        >
          <c-table
            class="box-card"
            v-model="model"
            :dsl="panelDsl.rows"
            :rules="APP.rules"
            :tableInfo="panelDsl"
            @eventAvatarUploadSucess="handleAvatarUploadSucess"
            ref="topTabe"
          ></c-table>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";

export default {
  name: "YqysYsJbzlAdd",
  inject: ["APP"],
  mixins: [Main],
  mounted() {},
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.warn("beforeRouteEnter", to.name);
      vm.activeNames = vm.APP.dslArray.map(panel => {
        return panel.title;
      });
      let command = to.name;
      const initmodel = {
        add() {
          /* 置空 */
          vm.model = (model => {
            let obj = {};
            for (let key in model) {
              obj[key] = "";
            }
            return obj;
          })(vm.APP.model);
          console.log(vm.model);
        },
        update() {
          /* 初始化新增model */
          vm.model = JSON.parse(JSON.stringify(vm.APP.model));
        }
      };
      initmodel[command] && initmodel[command]();
    });
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
  },
  beforeRouteUpdate(to, from, next) {
    next();
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
  },
  beforeRouteLeave(to, from, next) {
    next();
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
  },
  data() {
    return {
      activeNames: [],
      model: {}
    };
  },
  methods: {
    handleAvatarUploadSucess(res, item) {
      console.log("res, item", res, item);
    },
    goTo(command, isConfirm) {
      if (isConfirm) {
        this.$confirm(`确实离开?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$router.push({
              name: command
            });
          })
          .catch(error => {
            console.log("cancel", error);
          });
      } else {
        this.$router.push({
          name: command
        });
      }
    },
    async save() {
      console.log(this.APP.model);

      function isOk(tables) {
        let count = tables.length;
        let isAllOk = true;
        return new Promise(resolve => {
          tables.map(table => {
            table.valid().then(isSuccess => {
              --count;
              isAllOk = isAllOk && isSuccess;
              if (!isAllOk || count === 0) resolve(isAllOk);
            });
          });
        });
      }

      const loading = this.$loading();
      try {
        /* 验证表单是否都符合rule */
        if (await isOk(this.$refs.topTabe)) {
          this.APP.modelAdd = this.model;
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
        loading.close();
      }
    }
  },
  computed: {
    currentPath() {
      return this.$route.name;
    },
    isDisabled() {
      return this.currentPath === "detail";
    },
    title() {
      const titleList = {
        update: "更新",
        add: "新增",
        detail: "详情"
      };
      return "个人信息——" + titleList[this.currentPath];
    }
  }
};
</script>