<template>
  <div id="app">
    <transition name="silde-fade">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import { model, dslArray, rules } from "./mock.js";
import { dslTableHeader } from "./mock.js";
/* 新增个人资料信息 */
import { modelGRZL, dslArrayGRZL, rulesGRZL } from "./mock.js";
let MSG_OPTIONS = {};

export default {
  name: "AppRoot",
  provide() {
    return {
      APP: this
    };
  },
  data() {
    return {
      model,
      modelMainList: [],
      modelAdd: {},
      modelUpdate: {},
      modelDetail: modelGRZL,
      dslArray,
      rules,
      /* 个人信息列表 */
      dslTableHeader,
      /* 新增个人资料信息 */
      modelGRZL,
      dslArrayGRZL,
      rulesGRZL
      /* 新增个人资料信息 */
    };
  },
  methods: {
    goTo(command, isConfirm) {
      if (command === "remove") {
        this.$confirm(`确实要删除当前的表单?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            const loading = this.$loading();
            /* 发送this.currentModel到后台处理删除请求 */
            console.log(this.currentModel);
            setTimeout(() => {
              this.afterRemove(loading);
            }, 1000 * 2);
          })
          .catch(error => {
            console.log("cancel", error);
          });
      } else {
        if (isConfirm) {
          this.$confirm(`确实离开?`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.$router.push({
                name: command
              });
            })
            .catch(error => {
              console.log("cancel", error);
            });
        } else {
          this.$router.push({
            name: command
          });
        }
      }
    },
    async save({ loading } = {}) {
      /* loading框 */
      loading = loading || this.$loading();
      try {
        let doc = this.modelAdd;
        /* save */
        let response = await this.$post("/proxy/save", {
          coll: "fnbpc_hyjc" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          /* 刷新列表 */
          MSG_OPTIONS = {
            type: "success",
            message: "保存成功"
          };
          /* 保存成功，更新当前展示的 */
          this.model = this.modelAdd;
          this.goTo("home");
        } else {
          throw new Error(response.data);
        }
      } catch (error) {
        MSG_OPTIONS = {
          type: "error",
          message: error.message
        };
      } finally {
        loading.close();
        this.$message({ showClose: true, ...MSG_OPTIONS });
      }
    }
  },
  watch: {}
};
</script>

<style src="@c/styles/app.scss" lang="scss">
</style>
<style src="@c/styles/hqbj.scss" lang="scss">
</style>



